import React from 'react'
import Constants, { userToken } from "src/Constants";
import {
  CRow,
  CCol,
  CWidgetStatsF,
} from '@coreui/react'

import { Link } from 'react-router-dom';
// import {CIcon ,cilChartPie} from '@coreui/icons-react';
import CIcon from '@coreui/icons-react';
import { cilCheck, cilCamera, cilXCircle, cilArrowRight } from '@coreui/icons';
import { useUserStore } from 'src/userStore';

const WidgetsDropdown = () => {

  const hasPerm = useUserStore((state) => state.hasPerm);

  const[count, setCount] = React.useState(0)

  React.useEffect(() => {
    let url = Constants.counts;


    (async () => {
        let response = await fetch(url, {
            method: 'GET',
            headers: {
                'Authorization': userToken(),

            }
        })
        let data = await response.json();
        setCount(data.data)


    })()
}, [])

  return (
    <CRow>  
    <CCol xs={4} className={`flex-grow-1 ${!hasPerm('view_crossing') ? 'd-none': ''}`}>
      <CWidgetStatsF
        className="mb-3"
        color="success"
        title="Total Crossings"
        icon={<CIcon icon={cilCheck} height={36} />}
        value={count.totalCrossing}
        footer={
          <Link className="font-weight-bold font-xs text-medium-emphasis" to='/crossings'>
            View more
            <CIcon icon={cilArrowRight} className="float-end" width={16} />
          </Link>
        }
        />
    </CCol>
    <CCol xs={4} className={`flex-grow-1 ${!hasPerm('view_camera') ? 'd-none': ''}`}>
      <CWidgetStatsF
        className="mb-3"
        color="primary"
        title="Total Cameras"
        icon={<CIcon icon={cilCamera} height={36} />}
        value={count.totalCamera}
        footer={
          <Link className="font-weight-bold font-xs text-medium-emphasis" to='/cameras'>
            View more
            <CIcon icon={cilArrowRight} className="float-end" width={16} />
          </Link>
        }
        />
    </CCol>
    <CCol xs={4} className={`flex-grow-1 ${!hasPerm('view_closure') ? 'd-none': ''}`}>
      <CWidgetStatsF
        className="mb-3"
        color="danger"
        title="Total Closures"
        icon={<CIcon icon={cilXCircle} height={36} />}
        value={count.totalClosures}
        footer={
          <Link className="font-weight-bold font-xs text-medium-emphasis" to='/closures'>
            View more
            <CIcon icon={cilArrowRight} className="float-end" width={16} />
          </Link>
        }
        />
    </CCol>
    </CRow>
  )
}

export default WidgetsDropdown
